.footer {
    padding: 55px 0 55px;
    background: #242427;
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }
    &__left {
        font-weight: 300;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        &-year {
            font-family: 'Roboto';
            width: 116px;
        }
        &-company {
            font-family: 'Roboto';
            margin-top: 17px;
            width: 146px;
        }
        &-rf {
            margin-top: 22px;
            display: flex;
            align-items: center;
        }
        &-text {
            font-family: 'Roboto';
            width: 176px;
            margin-left: 11px;
        }
    }
    &__center {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    &__policy {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        text-decoration-line: underline;
        color: #FFFFFF;
    }
    &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        &-contact {
            display: flex;
            align-items: center;
        }
        &-icon {
            margin-right: 11px;
        }
        &-phone {
            font-family: 'Roboto';
            font-weight: 800;
            font-size: 18px;
            line-height: 21px;
            color: #FFFFFF;
        }
        &-text {
            margin-top: 4px;
            font-family: 'Roboto';
            font-weight: 300;
            font-size: 12px;
            line-height: 14px;
            color: #FFFFFF;
        }
        .franch-f5 {
            display: flex;
            &__icon {
                margin-right: 9px;
            }
            &__text {
                width: 177px;
                font-family: 'Roboto';
                font-weight: 300;
                font-size: 14px;
                line-height: 16px;
                color: #FFFFFF;
            }
        }
    }
}
