.modal__wrapper {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: auto;
    z-index: 11;
    display: none;
}

.modal {
    background-image: url('../img/modal_bg_1.jpg');
    width: 1040px;
    height: 590px;
    padding: 44px 62px 41px;
    box-shadow: 0px 4px 70px rgba(0, 0, 0, 0.25);
    border-radius: 16px;
    position: relative;
    &__close {
        cursor: pointer;
        position: absolute;
        top: -50px;
        right: 0;
    }
    &__title {
        width: 635px;
        font-weight: 600;
        font-size: 32px;
        line-height: 120%;
        text-transform: uppercase;
        color: #000000;
    }
    &__form {
        margin-top: 29px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
    }
    &__input {
        width: 380px;
        height: 66px;
        left: 504px;
        top: 407px;
        background: rgba(70, 70, 70, 0.1);
        border: none;
        margin-bottom: 8px;
        padding-left: 54px;
        &-phone {
            background-image: url('../img/icons/modal_icon_phone.svg');
            background-position-y: center;
            background-position-x: 20px;
            background-repeat: no-repeat;
        }
        &-mail {
            background-image: url('../img/icons/modal_icon_mail.svg');
            background-position-y: center;
            background-position-x: 20px;
            background-repeat: no-repeat;
        }
        &-name {
            background-image: url('../img/icons/modal_icon_name.svg');
            background-position-y: center;
            background-position-x: 20px;
            background-repeat: no-repeat;
        }
        &::placeholder {
            font-weight: 300;
            font-size: 14px;
            color: #0B0B0B;
            opacity: 0.6;
        }
    }
    &__btn {
        margin-top: 30px;
        width: 382px;
        height: 84px;
        left: 504px;
        top: 654px;
        background: linear-gradient(94.75deg, #003A92 46.16%, #0950BA 93.66%);
        box-shadow: 0px 4.14815px 41.4815px rgba(0, 0, 0, 0.15);
        font-weight: 450;
        font-size: 19px;
        line-height: 24px;
        color: #FFFFFF;
        cursor: pointer;
    }
    &__checkbox {
        margin-top: 22px;
        display: flex;
        align-items: flex-start;
        &-check {
            margin-right: 7px;
            position: absolute;
            z-index: -1;
            opacity: 0;
        }
        &-label {
            font-family: 'Montserrat';
            width: 340px;
            font-weight: 300;
            font-size: 14px;
            line-height: 120%;
            color: #181818;
            margin-left: 20px;
            &::before {
                content: "";
                width: 12px;
                height: 12px;
                background-color: #FFFFFF;
                display: inline-flex;
                align-items: flex-end;
                margin-right: 5px;
                border-radius: 2px;
                border: 1px solid #1E1E1E;
                display: block;
                position: absolute;
                left: 0;
            }


        }
    }
}
.modal__checkbox-check:checked+.modal__checkbox-label::before {
    background-image: url('../img/icons/modal_icon_check.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.input-error {
    background-color: red;
}
