.russian {
    background-image: url('../img/russian_bg.jpg');
    padding: 93px 0 117px;
    &__title {
        width: 643px;
        margin: 0 auto;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #FFFFFF;
    }
    &__descr {
        width: 978px;
        margin: 20px auto 0;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #FFFFFF;
    }
    &__wrapper {
        margin-top: 100px;
        display: flex;
    }
    &__left {
        width: 575px;
        padding: 58px 57px 65px 58px;
        background: rgba(255, 255, 255, 0.1);
        backdrop-filter: blur(5px);
        margin-right: 25px;
        &-title {
            width: 400px;
            font-weight: 500;
            font-size: 24px;
            line-height: 31px;
            text-transform: uppercase;
            color: #FFFFFF;
        }
        &-descr {
            margin-top: 40px;
            font-weight: 300;
            font-size: 20px;
            line-height: 26px;
            color: #FFFFFF;
        }
        &-line {
            width: 460px;
            border: 1px solid #FFFFFF;
            opacity: 0.5;
            margin: 49px 0;
        }
    }
    &__right {
        width: 560px;
        &-wrapper {
            padding: 38px 30px;
            background: rgba(255, 255, 255, 0.1);
            backdrop-filter: blur(5px);
        }
        &-title {
            display: flex;
        }
        &-title_img {
            width: 62px;
            height: 62px;
            background: rgba(255, 255, 255, 0.1);
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 14px;
        }
        &-title_text {
            width: 402px;
            font-weight: 500;
            font-size: 24px;
            line-height: 31px;
            text-transform: uppercase;
            color: #FFFFFF;
        }
        &-descr {
            margin-top: 20px;
            font-weight: 300;
            font-size: 18px;
            line-height: 23px;
            color: #FFFFFF;
        }
    }
}

.segments {
    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 31px;
        text-transform: uppercase;
        color: #FFFFFF;
    }
    &__wrapper {
        margin-top: 40px;
    }
    &__diagramma {
        position: relative;
        width: 230px;
        min-height: 230px;
        background:
		radial-gradient(
			circle closest-side,
			#3a393d 0,
			#3a393d 60%,
			transparent 60%,
			transparent 100%,
			#3a393d 0
		),
		conic-gradient(
			#616164 0,
			#616164 47%,
			#c8c8ca 0,
			#c8c8ca 72%,
			#b1b1b3 0,
			#b1b1b3 82%,
			#909091 0,
			#909091 100%
	);
    }
    &__wrapper {
        display: flex;
        align-items: center;
    }
    &__list {
        &-item {
            width: 175px;
            list-style: none;
            font-weight: 300;
            font-size: 20px;
            line-height: 26px;
            color: #FFFFFF;
            margin-bottom: 14px;
            padding-left: 19px;
            position: relative;
            &::before {
                content: '';
                width: 10px;
                height: 10px;
                background: #C9C9CA;
                display: block;
                position: absolute;
                left: 0;
                top: 10px;
            }
        }
    }
}
