.about {
    padding: 28px 0 120px;
    background: #F2F3F5;
    &__design {
        display: flex;
        align-items: center;
        &-descr {
            width: 656px;
        }
        &-title {
            width: 500px;
            font-weight: 500;
            font-size: 32px;
            line-height: 41px;
            text-transform: uppercase;
            font-feature-settings: 'tnum' on, 'lnum' on;
            color: #1B1B1D;
        }
        &-list {
            margin-top: 38px;
            padding-left: 21px;
            font-weight: 300;
            font-size: 20px;
            line-height: 26px;
            color: #000000;
        }
        &-item {
            position: relative;
        }
        &-item:not(:last-child) {
            margin-bottom: 30px;
        }
        &-item::before {
            content: '';
            width: 10px;
            height: 10px;
            background: #000000;
            display: block;
            position: absolute;
            left: -21px;
            top: 10px;
        }
    }
    &__box {
        display: grid;
        grid-template: repeat(2, 582px) / repeat(2, 580px);
        &-item {
            padding: 30px;
            &_01 {
                background-image: url('../img/about_box_bg1.jpg');
            }
            &_02 {
                background-image: url('../img/about_box_bg2.jpg');
            }
            &_03 {
                background-image: url('../img/about_box_bg3.jpg');
            }
            &_04 {
                position: relative;
                padding: 45px 0 0 45px;
            }
        }
        &-number {
            display: flex;
            justify-content: end;
        }
        &-title {
            margin-top: 12px;
            font-weight: 500;
            font-size: 32px;
            line-height: 41px;
            text-align: center;
            text-transform: uppercase;
            font-feature-settings: 'tnum' on, 'lnum' on;
            color: #1B1B1D;
            &_03 {
                margin-top: 42px;
            }
            &_04 {
                width: 325px;
                font-weight: 450;
            }
        }
        &-descr {
            width: 460px;
            margin: 15px auto 0;
            font-weight: 300;
            font-size: 18px;
            line-height: 23px;
            text-align: center;
            color: #000000;
            &_04 {
                margin-top: 12px;
                width: 202px;
                font-weight: 300;
                font-size: 14px;
                line-height: 18px;
                text-align: start;
                margin-left: 0;
            }
        }
        &-img {
            position: absolute;
            right: 0;
            bottom: 43px;
        }
    }
    &__midleprice {
        margin-top: 19px;
        width: 163px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
        &_white {
            width: 100%;
            height: 46px;
            background: #FFFFFF;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &_blue {
            width: 100%;
            height: 38px;
            background: linear-gradient(94.75deg, #003A92 46.16%, #0950BA 93.66%);
            font-weight: 700;
            font-size: 20px;
            line-height: 24px;
            color: #FFFFFF;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &::after {
            content: '';
            width: 243px;
            height: 0px;
            border: 2px solid #808080;
            background: #808080;
            position: absolute;
            right: -240px;
            bottom: 35px;
        }
    }
    &__segments {
        position: absolute;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        &-item {
            height: 38px;
            width: 178px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 700;
            font-size: 12px;
            line-height: 15px;
            color: #929497;
            border-left: 1px solid #929497;
            &:last-child {
                border-right: 1px solid #929497;
            }
        }
    }
}
