.start {
    background-image: url('../img/start_bg.jpg');
    background-position: center;
    background-size: cover;
    padding: 89px 0 72px;
    &__wrapper {
        margin-top: 80px;
        display: flex;
    }
    &__title {
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        color: #1B1B1D;
    }
    &__descr {
        width: 753px;
        margin: 20px auto 0;
        font-weight: 300;
        font-size: 20px;
        line-height: 26px;
        text-align: center;
        color: #000000;
    }
    &__left {
        width: 340px;
        margin-right: 16px;
    }
    &__right {
        width: 340px;
    }
    &__plan {
        width: 304px;
        margin: 210px auto 0;
        font-weight: 450;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #0B0B0B;
    }
    &__btn {
        width: 339px;
        height: 86px;
        margin: 27px auto 0;
        background: linear-gradient(94.75deg, #003A92 46.16%, #0950BA 93.66%);
        color: white;
        font-weight: 450;
        font-size: 19px;
        line-height: 24px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        &-img {
            margin-left: 25px;
        }
        &-input {
            display: none;
        }
    }
}

.accordeon {
    input[type="radio"], .accordeon input[type="checkbox"] {
        display: none;
    }
    &__wrapper-btn {
        margin-bottom: 10px;
    }
    &__btn {
        height: 73px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1em;
        font-weight: 700;
        font-size: 1.5em;
        background: #CFCFCF;
        cursor: pointer;
        &:hover {
            background: #242427;
            box-shadow: 0px 4px 100px rgba(0, 0, 0, 0.15);
            .accordeon__btn-title, .accordeon__btn-descr {
                color: white;
            }
            .accordeon__btn-img {
                visibility: hidden;
            }
        }
        &-title {
            font-weight: 500;
            font-size: 24px;
            line-height: 31px;
            text-align: center;
            color: #0B0B0B;
            &_white {
                color: #FFFFFF;
            }
        }
        &-descr {
            font-weight: 400;
            font-size: 16px;
            line-height: 21px;
            color: #0B0B0B;
            margin-right: 10px;
            &_white {
                color: #FFFFFF;
            }
        }
    }
    &__panel {
        height: 0;
        visibility: hidden;
        opacity: 0;
        color: #0B0B0B;
        font-weight: 300;
        font-size: 16px;
        line-height: 21px;
    }
}

/* Show .panel for corresponding to checked radio or checkbox */
.accordeon input[type="radio"]:checked + .accordeon__panel {
    height: auto;
    visibility: visible;
    opacity: 1;
    padding: 30px 34px 29px 25px;
    border: 1px solid #242427;
    ol {
        padding-left: 18px;
    }
    span {
        font-weight: 700;
    }
}
