.promo {
    height: 1038px;
    background-image: url('../img/promo_bg.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 23px;
    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-logo {
            width: 234px;
            img {
                width: 100%;
            }
        }
        &-text {
            font-weight: 300;
            font-size: 24px;
            line-height: 31px;
            color: #FFFFFF;
        }
    }
    &__contact {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-img {
            width: 30px;
            height: 30px;
            background: #171719;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 17px;
        }
        &-wrapper {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
        }
        &-phone {
            font-weight: 300;
            font-size: 20px;
            line-height: 26px;
            color: #000000;
        }
        &-text {
            font-weight: 500;
            font-size: 17px;
            line-height: 22px;
            text-align: center;
            color: #000000;
        }
    }
    &__wrapper {
        margin-top: 87px;
        display: flex;
        justify-content: space-between;
    }
    &__left {
        width: 532px;
        &-title {
            font-weight: 700;
            font-size: 40px;
            line-height: 51px;
            text-transform: uppercase;
            background: linear-gradient(90.28deg, #9C9C9C 30.19%, #DFDFDF 70.84%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            span {
                background: linear-gradient(90.28deg, rgba(223, 223, 223, 0) 30.19%, rgba(156, 156, 156, .3) 70.84%);
                padding-right: 10px;
            }
        }
        &-descr {
            margin-top: 6px;
            font-weight: 300;
            font-size: 40px;
            line-height: 51px;
            text-transform: uppercase;
            color: #FFFFFF;
        }
        &-text {
            margin-top: 26px;
            width: 370px;
            font-weight: 300;
            font-size: 21px;
            line-height: 28px;
            color: #FFFFFF;
        }
        &-commerce {
            margin-top: 68px;
            width: 387px;
            font-weight: 450;
            font-size: 24px;
            line-height: 31px;
            color: #FFFFFF;
        }
        &-btn {
            margin-top: 26px;
            width: 319px;
            height: 84px;
            background: linear-gradient(94.75deg, #003A92 46.16%, #0950BA 93.66%);
            box-shadow: 0px 4.14815px 41.4815px rgba(0, 0, 0, 0.15);
            border: none;
            font-weight: 450;
            font-size: 19px;
            line-height: 24px;
            color: #FFFFFF;
            cursor: pointer;
            &::after {
                content: url('../img/icons/promo_info-icon.svg');
                position: absolute;
                margin-left: 10px;
            }
        }
    }
    &__right {
        &-item {
            width: 270px;
            height: 118px;
            background: #FFFFFF;
            box-shadow: 0px 4px 90px rgba(182, 182, 182, 0.15);
            backdrop-filter: blur(17px);
            display: flex;
            align-items: center;
        }
        &-img {
            width: 56px;
            height: 51px;
            margin-right: 16px;
            img {
                width: 100%;
            }
        }
        &-text {
            width: 169px;
            span {
                font-weight: 450;
            }
        }
    }
}
