.history {
    background-color: #f2f4f5;
    padding: 105px 0 110px;
    &__wrapper {
        display: grid;
        grid-template: 1fr / 50% 50%;
        justify-items: end;
        gap: 5px;
    }
    &__list {
        width: 565px;
        padding-top: 20px;
        &-items {
            margin-top: 35px;
            padding-left: 21px !important;
        }
        &-item {
            font-weight: 300;
            font-size: 20px;
            line-height: 26px;
            color: #000000;
            position: relative;
            margin-bottom: 30px;
            &::before {
                content: '';
                width: 10px;
                height: 10px;
                background-color: #000000;
                display: block;
                position: absolute;
                left: -21px;
                top: 10px;
            }
        }
        &-text {
            font-weight: 500;
            font-size: 20px;
            line-height: 26px;
            text-transform: uppercase;
            color: #0B0B0B;
        }
    }
    &__title {
        width: 413px;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #1B1B1D;
    }
    &__grey {
        height: 682px;
        background-image: url('../img/history_grey-bg.jpg');
        display: flex;
        align-items: flex-start;
        position: relative;
        padding: 57px 0;
        &-wrapper {
            position: relative;
        }
        &-imgtitle {
            margin-bottom: 21px;
            img {
                display: block;
                margin: 0 auto;
            }
        }
        .tns-item {
            img {
                display: block;
                margin: 0 auto;
            }
        }
        &-prev, &-prev_right {
            width: 43px;
            height: 43px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 100px;
            &:hover {
                box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.35);
            }
        }
        &-next, &-next_right {
            width: 43px;
            height: 43px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 110px;
            &:hover {
                box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.35);
            }
        }
    }
    &__descr {
        width: 387px;
        margin: 0 auto;
        margin-top: 80px;
        font-weight: 450;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #0B0B0B;
    }
    &__btn {
        display: block;
        margin: 31px auto 0;
    }
}

.vertical-line {
    width: 1px;
    height: 568px;
    background-color: #FFFFFF;
    position: absolute;
    right: 50%;
    top: 50%;
    transform: translateY(-50%);
}
