.city {
    background-image: url('../img/city_bg.jpg');
    padding: 120px 0 152px;
    &__title {
        width: 700px;
        font-weight: 700;
        font-size: 45px;
        line-height: 58px;
        text-transform: uppercase;
        color: #242427;
        &-bg {
            background: linear-gradient(270deg, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0) 112.81%);
            padding-right: 10px;
        }
        &-light {
            font-weight: 400;
        }
    }
    &__wrapper {
        margin-top: 30px;
        display: flex;
    }
    &__left {
        width: 560px;
        font-weight: 300;
        font-size: 28px;
        line-height: 36px;
        color: #0B0B0B;
    }
    &__right {
        width: 358px;
        padding: 31px;
        font-weight: 500;
        font-size: 24px;
        line-height: 31px;
        color: #0B0B0B;
        background: rgba(255, 255, 255, 0.7);
        box-shadow: 0px 4px 90px rgba(182, 182, 182, 0.15);
        backdrop-filter: blur(17px);
    }
    &__descr {
        width: 462px;
        margin-top: 74px;
        font-weight: 450;
        font-size: 24px;
        line-height: 31px;
        color: #0B0B0B;
    }
    &__btn {
        margin-top: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        &::after {
            content: '';
        }
    }
}
