.formats {
    padding: 100px 0 98px;
    background-image: url('../img/formats_bg.jpg');
    &__title {
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #FFFFFF;
    }
    &__descr {
        margin: 30px auto 0;
        width: 986px;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #FFFFFF;
    }
    &__images {
        width: 1020px;
        margin: 60px auto 0;
        display: flex;
        justify-content: space-between;
    }
    &__image {
        &-left {
            position: relative;
            &:hover {
                .formats__image-infoleft {
                    opacity: 1;
                    transition: all 0.5s;
                }
            }
        }
        &-right {
            position: relative;
            &:hover {
                .formats__image-inforight {
                    opacity: 1;
                    transition: all 0.5s;
                }
            }
        }
        &-infoleft {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
            opacity: 0;
            padding: 34px 52px;
            display: flex;
            flex-direction: column;
            justify-content: end;
        }
        &-infotext {
            width: 233px;
            font-weight: 500;
            font-size: 22px;
            line-height: 29px;
            text-transform: uppercase;
            font-feature-settings: 'tnum' on, 'lnum' on;
            color: #FFFFFF;
        }
        &-inforight {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: rgba(95, 95, 95, 0.8);
            backdrop-filter: blur(7.5px);
            opacity: 0;
            padding: 39px 47px 39px 55px;
        }
        &-right-title {
            width: 232px;
            font-weight: 500;
            font-size: 22.2462px;
            line-height: 29px;
            text-transform: uppercase;
            font-feature-settings: 'tnum' on, 'lnum' on;
            color: #FFFFFF;
            margin-bottom: 25px;
        }
        &-right-item {
            padding: 0 0 19px 19px;
            font-weight: 400;
            font-size: 20px;
            line-height: 26px;
            color: #FFFFFF;
            border-bottom: 1px solid #FFFFFF;
            margin-bottom: 16px;
            position: relative;
            &:last-child {
                border-bottom: none;
            }
            &::before {
                content: '';
                width: 9px;
                height: 9px;
                background-color: #FFFFFF;
                display: inline-block;
                position: absolute;
                left: 0;
                top: 10px;
            }
        }
    }
    &__text {
        margin: 60px auto 0;
        width: 539px;
        font-weight: 400;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #FFFFFF;
    }
}
