.thanks {
    padding: 23px 0 88px;
    background-image: url('../img/thanks_bg.jpg');
    position: relative;
    &__contact {
        &-img {
            background: rgba(255, 255, 255, 0.2);
        }
        &-phone {
            color: #FFFFFF;
        }
        &-text {
            color: #FFFFFF;
        }
    }
    &__title {
        margin-top: 89px;
        width: 760px;
        font-weight: 700;
        font-size: 50px;
        line-height: 64px;
        text-transform: uppercase;
        background: linear-gradient(90.28deg, #9C9C9C 30.19%, #DFDFDF 70.84%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        span {
            background: linear-gradient(90.28deg, rgba(223, 223, 223, 0) 30.19%, rgba(156, 156, 156, .3) 70.84%);
            padding-right: 10px;
        }
    }
    &__connect {
        margin-top: 26px;
        width: 370px;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        color: #FFFFFF;
    }
    &__text {
        margin-top: 37px;
        width: 413px;
        font-weight: 300;
        font-size: 18px;
        line-height: 23px;
        color: #FFFFFF;
    }
    &__team {
        margin-top: 37px;
        font-weight: 700;
        font-size: 24px;
        line-height: 31px;
        color: #FFFFFF;
    }
    &__sait {
        margin-top: 15px;
        display: flex;
        align-items: center;
        &-text {
            font-weight: 300;
            font-size: 24px;
            line-height: 31px;
            color: #FFFFFF;
            margin-right: 14px;
        }
    }
    &__your {
        width: 472px;
        margin-top: 37px;
        font-weight: 700;
        font-size: 36px;
        line-height: 46px;
        color: #FFFFFF;
    }
    &__btn {
        margin-top: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 345px;
        height: 84px;
        font-weight: 450;
        font-size: 18.6667px;
        line-height: 24px;
        color: #FFFFFF;
        background: linear-gradient(94.75deg, #003A92 46.16%, #0950BA 93.66%);
        box-shadow: 0px 4.14815px 41.4815px rgba(0, 0, 0, 0.15);
    }
    &__circle {
        width: 823px;
        height: 823px;
        background-color: rgb(60 60 60);
        border-radius: 50%;
        position: absolute;
        top: 269px;
        right: 204px;
    }
}

.work-time {
    margin-top: 37px;
    width: 331px;
    height: 160px;
    padding: 17px 22px;
    background: rgba(255, 255, 255, 0.2);
    box-shadow: 0px 4px 90px rgba(182, 182, 182, 0.15);
    backdrop-filter: blur(17px);
    color: #FFFFFF;
    &__title {
        font-weight: 500;
        font-size: 24px;
        line-height: 31px;
        border-bottom: 1px solid #FFFFFF;
        padding-bottom: 13px;
    }
    &__wrapper {
        padding-top: 13px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__day {
        width: 84px;
        font-weight: 600;
        font-size: 24px;
        line-height: 31px;
        text-transform: uppercase;
    }
    &__text {
        width: 160px;
        font-weight: 600;
        span {
            display: block;
            font-weight: 300;
            font-size: 18px;
            line-height: 23px;
        }
    }
}
