.partner {
    background-image: url('../img/partner_bg.jpg');
    padding: 97px 0 107px;
    &__title {
        width: 771px;
        margin: 0 auto;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #FFFFFF;
    }
    &__descr {
        width: 978px;
        margin: 20px auto 0;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #FFFFFF;
    }
    &__wrapper {
        margin-top: 19px;
        display: flex;
    }
    &__list-left {
        width: 394px;
        padding-top: 60px;
    }
    &__list-item {
        display: flex;
        margin-bottom: 23px;
    }
    &__list-number {
        width: 41px;
        height: 41px;
        padding: 9px 10px;
        background: rgba(255, 255, 255, 0.1);
        font-family: 'Cormorant';
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #FFFFFF;
        margin-right: 19px;
    }
    &__list-text {
        font-weight: 300;
        font-size: 18px;
        line-height: 23px;
        color: #FFFFFF;
        span {
            font-weight: 450;
        }
    }
    &__list-right {
        width: 309px;
        padding-top: 60px;
        margin-left: 74px;
    }
}


@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.dropdown {
	&__wrapper {
		display: grid;
		grid-template: 63px / 556px 283px 312px;
		gap: 4px;
	}
	&__table-name {
		width: 556px;
		background: #FFFFFF;
		font-weight: 450;
		font-size: 24px;
		line-height: 31px;
		color: #0B0B0B;
		display: flex;
		align-items: center;
		padding-left: 30px;
	}
	&__table-price {
		width: 283px;
		background: #FFFFFF;
		font-weight: 450;
		font-size: 24px;
		line-height: 31px;
		text-align: center;
		color: #0B0B0B;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__action {
		width: 312px;
		height: 63px;
		background: linear-gradient(94.75deg, #003A92 46.16%, #0950BA 93.66%);
		box-shadow: 0px 4px 100px rgba(0, 0, 0, 0.15);
		font-weight: 450;
		font-size: 24px;
		line-height: 31px;
		color: #FFFFFF;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding: 0 35px 0;
		user-select: none;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		&-text {
			margin-right: 39px;
		}
	}
	&__caret-down {
		width: 25px;
		height: 14px;
	}
	&__content {
		margin-top: 4px;
		padding: 0 0.2em;
		max-height: 0;
		transition: max-height 0.5s ease-in-out;
		overflow: hidden;
		background-color: inherit;
		padding: 0;
		display: flex;
		align-items: center;
		flex-direction: column;
		&-table {
			display: grid;
			grid-template: repeat(7, 50px) 87px repeat(3, 50px) / 556px 600px;
			gap: 4px;
		}
		&-row {
			height: 50px;
		}
		&-column1 {
			width: 556px;
			background: rgba(255, 255, 255, 0.2);
			font-weight: 300;
			font-size: 24px;
			line-height: 31px;
			color: #FFFFFF;
			padding: 0 30px;
			display: flex;
			align-items: center;
			&_grey {
				background: rgba(255, 255, 255, 0.5);
				font-weight: 500;
			}
		}
		&-column2 {
			width: 600px;
			background: rgba(255, 255, 255, 0.1);
			font-weight: 450;
			font-size: 24px;
			line-height: 31px;
			color: #FFFFFF;
			padding-left: 79px;
			display: flex;
			align-items: center;
			&_grey {
				background: rgba(255, 255, 255, 0.5);
				font-weight: 500;
			}
		}
	}
}

.dropdown__action .dropdown__caret-down {
	height: 100%;
	display: flex;
	align-items: center;
}

.dropdown__action.active-dropdown span img {
	transform: rotateZ(180deg);
	transition: 0.5s all;
}

.dropdown__action.active-dropdown + .dropdown__content * {
	animation: fadeIn 0.5s ease-in-out;
	opacity: 1;
}
