.fashion {
    background-color: #f2f3f5;
    padding: 89px 0 80px;
    &__title {
        width: 800px;
        margin: 0 auto;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #1B1B1D;
    }
    &__descr {
        margin-top: 20px;
        font-weight: 300;
        font-size: 20px;
        line-height: 26px;
        text-align: center;
        color: #000000;
    }
    &__title-small {
        width: 581px;
        margin: 60px auto 0;
        font-weight: 450;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #0B0B0B;
    }
    &__grid {
        margin-top: 40px;
        width: 1157px;
        height: 770px;
        display: grid;
        grid-template: 380px 154px 226px / 300px 247px 230px 350px;
        gap: 10px;
        // &_item {

        // }
        &-item_1 {
            grid-column: 1 / 3;
            img {
                height: 100%;
            }
        }
        &-item_3 {
            grid-column: 4 / 5;
            grid-row: 1 / 3;
            img {
                height: 100%;
            }
        }
        &-item_4 {
            grid-row: 2 / 4;
            img {
                height: 100%;
            }
        }
        &-item_5 {
            grid-column: 2 / 4;
            grid-row: 2 / 4;
            img {
                height: 100%;
            }
        }
        &-item_6 {
            grid-column: 4 / 5;
            grid-row: 3 / 4;
            background-color: #e8e9ea;
            font-weight: 300;
            font-size: 35px;
            line-height: 45px;
            text-transform: uppercase;
            color: #0B0B0B;
            padding: 23px 35px;

        }
    }
    &__bottom-text {
        margin: 60px auto 0;
        width: 474px;
        font-weight: 450;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #0B0B0B;
    }
    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 31px auto 0;
        &::after {
            content: '';
        }
    }
    &__btn-text {
        margin-right: 10px;
    }
}
