.profit {
    padding: 80px 0 123px;
    background-image: url('../img/profit_bg1.jpg');
    position: relative;
    &__title {
        width: 715px;
        margin: 0 auto;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
    }
    &__descr {
        width: 742px;
        margin: 23px auto 0;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #FFFFFF;
    }
    &__girl {
        width: 810px;
        height: 897px;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 0;
    }
    &__table {
        margin-top: 89px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        &-item {
            padding: 30px 0 30px;
            font-weight: 450;
            font-size: 24px;
            line-height: 31px;
            text-transform: uppercase;
            color: #FFFFFF;
            border-bottom: 0.5px solid #FFFFFF;
            display: grid;
            grid-template: 1fr / 370px 286px;
            gap: 44px;
            z-index: 10;
            &:last-child {
                border-bottom: none;
            }
        }
        &-numbers {
            width: 286px;
            font-weight: 300;
            text-transform: none;
        }
    }
}
