.collection {
    background-image: url('../img/collection_bg.jpg');
    padding: 93px 0 77px;
    &__title {
        width: 669px;
        margin: 0 auto;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #1B1B1D;
    }
    &__descr {
        margin: 23px auto 0;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        color: #000000;
    }
    &__wrapper {
        margin-top: 65px;
        display: flex;
    }
    &__left {
        height: 736px;
        width: 598px;
    }
    &__right {
        width: 480px;
        position: relative;
    }
    &__bottom-text {
        width: 474px;
        margin: 44px auto 0;
        font-weight: 450;
        font-size: 24px;
        line-height: 31px;
        text-align: center;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #0B0B0B;
    }
    &__btn {
        &::after {
            content: '';
        }
        width: 306px;
        margin: 31px auto 0;
        display: flex;
        justify-content: center;
        align-items: center;
        &-text {
            margin-right: 25px;
        }
        &-img {
            width: 35px;
            height: 36px;
        }
    }
}

.scroll-bar {
    height: 736px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
        width: 6px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #39393C;
    }
    &__item {
        &-img {
            position: relative;
        }
        &-number {
            font-family: 'Cormorant';
            position: absolute;
            font-size: 67px;
            color: #242427;
            font-weight: 500;
            line-height: 82px;
            top: 10px;
            right: 40px;
        }
        &-title {
            margin-top: 21px;
            font-weight: 450;
            font-size: 18px;
            line-height: 23px;
            text-transform: uppercase;
            color: #000000;
        }
        &-text {
            margin: 6px 0 30px;
            font-weight: 300;
            font-size: 14px;
            line-height: 18px;
            color: #000000;
        }
    }
}
