.choice {
    padding: 89px 0 122px;
    &__title {
        margin: 0 auto;
        font-weight: 500;
        font-size: 32px;
        line-height: 41px;
        text-align: center;
        text-transform: uppercase;
        font-feature-settings: 'tnum' on, 'lnum' on;
        color: #1B1B1D;
    }
    &__descr {
        width: 748px;
        margin: 14px auto 0;
        font-weight: 300;
        font-size: 20px;
        line-height: 26px;
        text-align: center;
        color: #000000;
    }
    &__wrapper {
        margin-top: 60px;
        position: relative;
    }
    &__slider {
        &-img {
            width: 337px;
            margin: 0 auto;
        }
        &-descr {
            width: 337px;
            height: 176px;
            margin: 0 auto;
            padding: 30px 25px 0;
            background-color: #f4f4f5;
            font-weight: 300;
            font-size: 16px;
            line-height: 21px;
            text-align: center;
            color: #000000;
        }
        &-name {
            width: 337px;
            margin: 0 auto;
            background-color: #dededf;
            padding: 9px 0 9px;
            font-weight: 600;
            font-size: 16.8557px;
            line-height: 22px;
            text-align: center;
            color: #000000;
        }
        &-prev, &-next {
            width: 43px;
            height: 43px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #DEDEDF;
            border: none;
            position: absolute;
        }
        &-prev {
            left: -40px;
            top: 50%;
            transform: translateY(-50%);
        }
        &-next {
            right: -40px;
            top: 50%;
            transform: translateY(-50%);
        }

    }
}
